<template>
    <section>
      <h1 class="has-text-primary is-size-4">My Pastes</h1>
      <hr>
      <b-table
          :data="pastes"
          v-if="thereArePaste(pastes.length)"
          ref="table"
          paginated
          per-page="10"
          pagination-position="both"
          :opened-detailed="defaultOpenedDetails"
          detailed
          detail-key="id"
          @details-open="(row) => $buefy.notification.open({
            message: `Expanded ${row.name}`,
            type: 'is-success'
            })"
          :show-detail-icon="showDetailIcon"
          aria-next-label="Next page"
          aria-previous-label="Previous page"
          aria-page-label="Page"
          aria-current-label="Current page">

          <b-table-column field="name" label="Name" v-slot="props">
              <template v-if="showDetailIcon">
                  {{ props.row.name }}
              </template>
              <template v-else>
                  <a @click="toggle(props.row)">
                      {{ props.row.name }}
                  </a>
              </template>
          </b-table-column>

          <b-table-column field="created_at" label="Date" v-slot="props">
              <span class="tag is-success">
                  {{ new Date(props.row.created_at).toLocaleDateString() }}
              </span>
          </b-table-column>

          <b-table-column field="linkLength" label="Links" v-slot="props">
            {{ props.row.links.length }}
          </b-table-column>

          <b-table-column field="options" label="Options" v-slot="props">
            <div class="buttons">
              <b-button
                type="is-primary"
                label="Editar"
                tag="router-link"
                :to="{ name: 'PasteEdit', params: { id: props.row.id }}"
                class="mr-1"></b-button>

              <button :class="`button ${dialogDeletePaste.type}`" @click="confirmCustomDelete(props.row)">
                  {{ dialogDeletePaste.textButton }}
              </button>

            </div>
          </b-table-column>

          <template slot="detail" slot-scope="props">
              <article class="media">
                  <div class="media-content">
                      <div class="content">
                          <p>
                            {{ props.row.body }}
                          </p>
                          <ul>
                            <li v-for="item in props.row.links" :key="item.id">
                              <span :class="`tag ${tagType(item.status)}`">{{ item.name }} - {{ item.identy }}</span>
                            </li>
                          </ul>
                      </div>
                  </div>
              </article>
          </template>
      </b-table>

      <div v-else>
        <b-notification
          type="is-info"
          has-icon
          :closable="false">
          There are nothing here!! please create new paste :)
        </b-notification>
      </div>

    </section>
</template>

<script>
// import DialogPasteDelete from '@/components/app/DialogPasteDelete.vue'
import Paste from '../../api/Paste'

export default {
    name: 'TablePaste',
    data() {
        return {
          pastes: [],
          defaultOpenedDetails: [],
          showDetailIcon: true,
          dialogDeletePaste: {
              title: 'Delete Paste',
              message: 'Are you sure delete this Paste?',
              textButton: 'Delete',
              confirmText: 'Delete Paste',
              type: 'is-danger',
              hasIcon: true,
              confirm: 'Delete Paste Successful',
            },
        }
    },
    methods: {
      getPastes: function() {
        Paste.getPastes().then(response => {

         this.pastes = response.data
       }).catch( (error) => {
         console.log(error)
       })
      },
      toggle: function(row) {
          this.$refs.table.toggleDetails(row)
      },

      thereArePaste: function(data) {
        return data !== 0
      },

      confirmCustomDelete(data) {
        this.$buefy.dialog.confirm({
            title: this.dialogDeletePaste.title,
            message: this.dialogDeletePaste.message,
            confirmText: this.dialogDeletePaste.confirmText,
            type: this.dialogDeletePaste.type,
            hasIcon: this.dialogDeletePaste.hasIcon,
            onConfirm: () => {
              console.log(data)
              Paste.deletePaste(data).then((response) => {
                if(response.status === 200) {
                  this.$buefy.notification.open({
                    message: this.dialogDeletePaste.confirm,
                    type: 'is-success'
                  })
                  this.getPastes()
                }
              }).catch((error) => {
                if(error.response.status === 404){
                  this.$buefy.toast.open({
                    message: 'Not Found',
                    type: 'is-success',
                    position: 'is-top-right'
                  })
                }
              })
            }
        })
      },

      tagType: function (status) {
        if(status === 'on'){
          return 'is-success'
        }

        if(status === 'report'){
          return 'is-warning'
        }

        if(status === 'off'){
          return 'is-danger'
        }
      }
    },
    mounted () {
      this.getPastes()

    }

}
</script>
