import Api from './Api'
import Csrf from './Csrf'


export default {

  async getPastes () {
    await Csrf.getCookie()
    return Api.get('paste')
  },

  async createPaste (form) {
    await Csrf.getCookie()
    return Api.post('paste/create', form)
  },

  async deletePaste (data) {
    const token = localStorage.getItem('access_token')
    await Csrf.getCookie()
    return Api.delete('/paste/delete/' + data.id , {
      headers: {
                Authorization: `Bearer ${token}`,
            }
    })
  },

  async getWebPaste(id) {
    await Csrf.getCookie()

    return await Api.get(`web/paste/${id}`)
  },

  async getWebPastes() {

    return await Api.get(`web/pastes`)
  },

  async linkReport(id){
    await Csrf.getCookie()

    return await Api.put(`web/linkreport/${id}`)
  }

}
